.mockup {
  position: relative; }

.mockup.-responsive {
  max-width: 100%; }

.mockup > .img {
  background-image: image_url("browser-mockup.png");
  background-repeat: no-repeat;
  background-size: 100%;
  height: 540px;
  width: 1290px;
  height: 0;
  padding-bottom: 41.86047%;
  position: relative;
  width: 100%; }
  @media only screen and (min-resolution: 1.3dppx) {
    .svg .mockup > .img {
      background-image: image_url("browser-mockup.svg");
      background-size: 100%; } }

.mockup > .body {
  height: 81.48148%;
  left: 1.93798%;
  overflow: hidden;
  position: absolute;
  top: 14.07407%;
  width: 96.12403%; }

.mockup > .body > img {
  width: 100%; }

a.mockup {
  display: block;
  transform: translate(0, 0);
  transition: transform 0.1s; }
  a.mockup:hover {
    transform: translate(0, -5%); }
