.navbar2 .nav {
  display: inline-block;
  height: 50px; }
  .navbar2 .nav > li {
    float: left;
    margin-bottom: 0; }
    .navbar2 .nav > li > a {
      color: #7d7b7a;
      padding: 13px 15px; }
      .navbar2 .nav > li > a > span + span {
        margin-left: 10px; }
      .navbar2 .nav > li > a:hover {
        background: none;
        color: #3c3c3b; }
  .navbar2 .nav > li.active > a {
    color: #353531;
    font-weight: bold;
    position: relative; }
    .navbar2 .nav > li.active > a:after {
      background: #FFCC33;
      bottom: 0;
      content: "";
      height: 4px;
      left: 10px;
      position: absolute;
      right: 10px; }
    .navbar2 .nav > li.active > a:hover {
      color: #000; }
