@import "bootstrap/variables/base";
@import "bootstrap/variables/font-family";
@import "bootstrap/variables/monospace";
@import "bootstrap/mixins/rh";

// Components

code,
kbd,
samp {
  font-family: $font-family-monospace;
}

code {
  background-color: $code-background-color;
  border-radius: $base-border-radius;
  color: $code-color;
  font-size: 90%;
  padding: 2px 4px;
}

kbd {
  background-color: $kbd-background-color;
  border-radius: $base-sm-border-radius;
  box-shadow: inset 0 -1px 0 rgba(0,0,0,.25);
  color: $kbd-color;
  font-size: 90%;
  padding: 2px 4px;

  kbd {
    box-shadow: none;
    font-size: 100%;
    font-weight: bold;
    padding: 0;
  }
}

// Context

@if $font-family-monospace-custom != none {
  .wf-active {
    code,
    kbd,
    samp {
      font-family: $font-family-monospace-custom, $font-family-monospace;
    }
  }
}
