.hljs {
  padding: 12px 15px; }

.cke_combo_text {
  width: 80px !important; }

.cke_combopanel {
  min-height: 228px; }

.textcomplete-dropdown {
  height: auto !important; }
  .textcomplete-dropdown img {
    width: 20px;
    margin-right: 10px; }

.js-ckeditor {
  min-height: 271px; }

.emoji {
  height: 20px;
  margin-bottom: 2px;
  width: 20px; }

figure {
  margin-bottom: 12px;
  text-align: center; }
  figure img {
    border-radius: 4px;
    height: auto;
    max-width: 100%; }
  figure figcaption {
    font-style: italic; }

@media (max-width: 767px) {
  h1,
  .h1 {
    font-size: 24px; } }
.media {
  margin-bottom: 12px;
  margin-top: 0; }

.rich-text img:not(.emoji) {
  border-radius: 4px;
  height: auto;
  max-width: 100%; }

.bootstrap-datetimepicker-widget {
  display: block; }

.form-control.js-date, .form-control.js-time {
  background-color: #fff; }

.media {
  overflow: visible; }

.img-cover {
  background-position: 50% 50%;
  background-size: cover;
  border-radius: 2px;
  border: 1px solid rgba(86, 85, 84, 0.3);
  cursor: -webkit-zoom-in;
  display: block;
  height: 24px;
  margin: 0 auto;
  transform-origin: 25% 50%;
  transform: scale(1);
  transition: transform 0.25s;
  width: 24px; }
  .img-cover:hover, .img-cover:focus {
    transform: scale(3); }

.media-hit p {
  margin-bottom: 0; }

.label-group {
  margin: 0 -2px; }
  .label-group .label {
    margin: 0 2px; }

.form-control-invisible {
  bottom: 0;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  width: 100%; }

pre {
  -webkit-overflow-scrolling: touch;
  overflow-x: scroll; }
  pre::-webkit-scrollbar {
    height: 10px;
    width: 10px; }
  pre::-webkit-scrollbar-thumb {
    background-clip: padding-box;
    background-color: rgba(255, 255, 255, 0.25);
    border-radius: 10px;
    border: 2px solid transparent; }
    pre::-webkit-scrollbar-thumb:hover {
      background-color: rgba(255, 255, 255, 0.5); }

pre code {
  white-space: pre;
  word-wrap: normal; }
