@import "bootstrap/mixins/list-unstyled";
@import "bootstrap/mixins/rh";

// Components

ul,
ol {
  margin-bottom: rh(0.5);
  margin-top: 0;
}

// Elements

ul,
ol {
  ul,
  ol {
    margin-bottom: 0;
  }
}

// Modificators

.list-inline {
  @include list-unstyled;
  margin-left: -5px;

  > li {
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
  }
}

.list-unstyled {
  @include list-unstyled;
}
