section.-mockup {
  color: #fff;
  overflow: hidden;
  padding: 10% 0 0;
  position: relative;
  text-align: center;
  z-index: 0; }
  section.-mockup:before {
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1; }
  section.-mockup .mockup {
    margin-bottom: -10%; }

section.-mockup.-without-image .mockup {
  margin-bottom: -30%; }

section.-mockup.-to-dark:before {
  background-image: radial-gradient(at top, transparent, rgba(0, 0, 0, 0.4)); }

section.-mockup.-from-light {
  color: #565554; }
  section.-mockup.-from-light:before {
    background-image: radial-gradient(at top, rgba(255, 255, 255, 0.7), transparent); }

.panel > section.-mockup {
  padding-left: 15px;
  padding-right: 15px; }
