.navbar2 {
  background: #fff;
  box-shadow: 0 10px 50px rgba(0, 0, 0, 0.04);
  position: relative; }

.navbar2 .pull-left,
.navbar2 .pull-right {
  height: 50px; }
.navbar2 .pull-left {
  text-align: left; }
  .navbar2 .pull-left > :first-child {
    margin-left: -15px; }
.navbar2 .pull-right {
  text-align: right; }
  .navbar2 .pull-right > :last-child {
    margin-right: -15px; }
