.hr-with-title {
  margin-bottom: 24px;
  margin-top: 0;
  position: relative;
  text-align: center; }
  .hr-with-title > span,
  .hr-with-title > a {
    padding: 0 5px;
    position: relative;
    text-transform: uppercase;
    z-index: 1; }
  .hr-with-title:before {
    border-top: 1px dashed #565554;
    content: "";
    height: 0;
    left: 0;
    position: absolute;
    top: 50%;
    width: 100%; }
