footer {
  padding: 36px 0; }
  footer p {
    margin-bottom: 0; }

html.with-sticky-footer {
  height: auto;
  min-height: 100%;
  position: relative; }
  html.with-sticky-footer body {
    height: auto;
    margin-bottom: 96px;
    position: initial; }
    html.with-sticky-footer body .body-before-footer {
      background: #f3f3f3; }
    html.with-sticky-footer body footer {
      bottom: 0;
      height: 96px;
      min-width: 100%;
      position: absolute; }

footer.bg-dark {
  box-shadow: 0 3px 20px rgba(0, 0, 0, 0.3) inset; }
